	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>When I began to rebrand Whole Foods, I looked at one of the major factor that products will use Amazon’s infrastructure by offering high quality food at competitive prices at customer’s doorstep. The organization highly values sustainability, and health and nutrition. Business and growth is always the priority of the Whole Foods. They support many positive values and take part in many active cultures and I wanted to emphasize and implement these qualities to more extent through rebranding. I wanted to build an identity more interconnected, consistent, meaningful, and accessible branding.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.25000000000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Main Logo\"\n        title=\"\"\n        src=\"/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-755ae.jpg\"\n        srcset=\"/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-f07fc.jpg 125w,\n/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-9d06f.jpg 250w,\n/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-755ae.jpg 500w,\n/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-63ca3.jpg 750w,\n/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-9b9a3.jpg 1000w,\n/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-6e031.jpg 1500w,\n/the-next-website/static/logo-01-bc20c580db442cdd33febc1759c60ca6-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>In the beginning, I began to allocate some new essence of words to the brand that would become an important part of rebranding. Healthy, Accessible and Individual. </p>\n<p>The next step was to select a new tagline that would become an interesting theme “Live Better Everyday” this would be a powerful introduction of the idea of prioritizes your health and focus on creating lifestyle.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.64999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Stationary \"\n        title=\"\"\n        src=\"/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-755ae.jpg\"\n        srcset=\"/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-f07fc.jpg 125w,\n/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-9d06f.jpg 250w,\n/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-755ae.jpg 500w,\n/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-63ca3.jpg 750w,\n/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-9b9a3.jpg 1000w,\n/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-6e031.jpg 1500w,\n/the-next-website/static/qureshi_letter-f060d3f4905ecc1e0be000c69eaad915-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>While designing a new identity of Whole Foods, I want to represent the natural and welcoming persona. Dark bottle green was the signature choice to represent the company as a whole for what they represent and relate with easily. I wanted to keep the colors natural and organic related which helps to maintain the essence of bright and rich identity similar to their products. Green can help enhance vision, stability and endurance. The dark green color has a healing power to understand their consumers to trust this new identity.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.64999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"qureshi rebrand\"\n        title=\"\"\n        src=\"/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-755ae.jpg\"\n        srcset=\"/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-f07fc.jpg 125w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-9d06f.jpg 250w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-755ae.jpg 500w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-63ca3.jpg 750w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-9b9a3.jpg 1000w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-6e031.jpg 1500w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>![](../assets/six applications-11.jpg)</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 64.69999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-755ae.jpg\"\n        srcset=\"/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-f07fc.jpg 125w,\n/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-9d06f.jpg 250w,\n/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-755ae.jpg 500w,\n/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-63ca3.jpg 750w,\n/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-9b9a3.jpg 1000w,\n/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-6e031.jpg 1500w,\n/the-next-website/static/van-05-95af3470a01ec675fad7e79caa82297d-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>![Phone app](../assets/six applications-04.jpg)</p>","frontmatter":{"title":"Whole Foods-Rebranding ","graduate":"Anam Qureshi ","category":"Brand Identity","category2":"Typography","category3":"Product Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-a7a62.jpg","srcSet":"/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-5a1a9.jpg 375w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-0908c.jpg 750w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-a7a62.jpg 1500w,\n/the-next-website/static/qureshi_rebrand-de739a329d073d25edf3a2413d0e59d9-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/whole-foods-rebranding/","name":"Whole Foods-Rebranding "}}

